h1,
h2,
h3,
h4 {
  margin-bottom: 1.25rem;
}

p,
blockquote {
  line-height: 1.6;
}

a,
a:visited,
a:active {
  text-decoration: none;
  background-color: transparent;
}

ul {
  list-style-type: none;
}

table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  text-align: left;
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* Remove all animations and transitions for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  * {
    transition-duration: 0.01ms !important;
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    scroll-behavior: auto !important;
  }
}
.sr-only {
  position: absolute !important;
  margin: -1px !important;
  clip: rect(1px, 1px, 1px, 1px) !important;
  -webkit-clip-path: inset(50%) !important;
  clip-path: inset(50%) !important;
  border: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  overflow: hidden !important;
  white-space: nowrap !important;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

::selection {
  background-color: hsl(296deg, 62%, 43%);
  color: hsl(203deg, 44%, 93%);
}

::-moz-selection {
  background-color: hsl(296deg, 62%, 43%);
  color: hsl(203deg, 44%, 93%);
}

html {
  box-sizing: border-box;
  font-size: 100%;
  font-family: "Karla", sans-serif;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  max-height: 100%;
  min-height: 100vh;
  background-color: #e5eff5;
}

.btn {
  display: inline-block;
  border: none;
  margin: 0;
  padding: 1rem 2rem;
  text-decoration: none;
  font-family: sans-serif;
  font-size: 1rem;
  line-height: 1;
  cursor: pointer;
  text-align: center;
  transition: background 250ms ease-in-out, transform 150ms ease;
  -webkit-appearance: none;
  -moz-appearance: none;
}

.btn:hover,
.btn:focus {
  background: #0053ba;
}

.btn:focus {
  outline: none;
  outline-offset: -4px;
}

.btn:active {
  transform: scale(0.99);
}

.card-about {
  background-color: hsla(179deg, 62%, 43%, 0.791);
  color: hsl(0deg, 0%, 100%);
  padding: 2.125rem;
  border-radius: 0 0 0.5rem 0.5rem;
}
@media (min-width: 42.5em) {
  .card-about {
    border-radius: 0 0 0.5rem 0;
  }
}

.card-table {
  width: 100%;
  display: flex;
}
.card-table th {
  font-size: 1.125rem;
  font-weight: 400;
  padding-bottom: 0.625rem;
}
.card-table td {
  font-size: 0.875rem;
  font-weight: 400;
  line-height: 1.4;
  color: hsla(0deg, 0%, 100%, 0.63);
}

.card-info {
  background-color: hsl(0deg, 0%, 100%);
  padding: 2.125rem;
  border-radius: 0.5rem 0.5rem 0 0;
}

.card-info-title {
  color: hsl(179deg, 62%, 43%);
  font-size: 1.75rem;
  font-weight: 700;
}

.card-info-subtile {
  color: hsl(71deg, 73%, 54%);
  font-size: 1.125rem;
  font-weight: 700;
}

.card-info-description {
  color: hsl(217deg, 22%, 67%);
  font-size: 1rem;
  font-weight: 400;
}

.card-subscription {
  display: grid;
  background-color: hsl(179deg, 62%, 43%);
  color: hsl(0deg, 0%, 100%);
  padding: 2.125rem;
  border-radius: 0;
}
@media (min-width: 42.5em) {
  .card-subscription {
    border-radius: 0 0 0 0.5rem;
  }
}

.card-subscription-title {
  font-size: 1.125rem;
}

.card-subscription-price-wrapper {
  display: flex;
  align-items: center;
}

.card-subscription-price {
  font-size: 1.75rem;
  font-weight: 700;
  margin-right: 0.75rem;
}

.card-subscription-date {
  color: hsla(0deg, 0%, 100%, 0.63);
}

.card-subscription-description {
  font-size: 1rem;
  font-weight: 400;
}

.btn-green {
  background: hsl(71deg, 73%, 54%);
  margin: 0.9375rem 0 0.9375rem 0;
  color: hsl(0deg, 0%, 100%);
  font-weight: 700;
  align-items: stretch;
  border-radius: 0.375rem;
}
.btn-green:hover {
  background: #accb20;
  transition: all 0.3s ease;
}
.btn-green:active {
  background: #6c8014;
}

.btn-green:hover,
.btn-green:focus {
  background: hsla(71deg, 73%, 54%, 0.729);
}
.btn-green:hover:hover,
.btn-green:focus:hover {
  background: rgba(172, 203, 32, 0.729);
  transition: all 0.3s ease;
}
.btn-green:hover:active,
.btn-green:focus:active {
  background: rgba(108, 128, 20, 0.729);
}

.card-widget {
  display: flex;
  flex-direction: column;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  margin: 1rem;
}
@media (min-width: 42.5em) {
  .card-widget {
    display: grid;
    grid-template-rows: repeat(1, 1fr);
    grid-template-columns: repeat(12, 1fr);
    max-width: 39.375rem;
    grid-auto-columns: auto;
  }
}

.card-info {
  grid-column: span 12;
}

.card-subscription,
.card-about {
  grid-column: span 6;
}